import { Component, OnInit } from '@angular/core';

import { AuthService } from 'sb-shared-lib';


@Component({
  selector: 'apps',
  templateUrl: 'apps.component.html',
  styleUrls: ['apps.component.scss']
})
export class AppsComponent implements OnInit {

  public user_apps: string[];

  private colors = ['3F52B5', '653BB5', '29A1A1', 'FF9741', 'F9BD15', 'E95367', '83C33F'];

  public apps:any = {
    booking: {
      url: "/booking",
      name: "APPS_APP_BOOKING",
      icon: 'airline_seat_individual_suite',
      color: this.colors[0]
    },
    pos: {
      url: "/pos",
      name: "APPS_APP_POS",
      icon: 'point_of_sale',
      color: this.colors[1]
    },
    catalog: {
      url: "/sales",
      name: "APPS_APP_SALES",
      icon: 'import_contacts',
      color: this.colors[2]
    },
    config: {
      url: "/settings",
      name: "APPS_APP_SETTINGS",
      icon: 'settings',
      color: this.colors[3]
    },
    documents: {
      url: "/documents",
      name: "APPS_APP_DOCUMENTS",
      icon: 'insert_drive_file',
      color: this.colors[4]
    }
  };

  constructor(
    private auth: AuthService) {
  }
  
  public async ngOnInit() {
    this.auth.getObservable().subscribe( (user:any) => {
      if(user.id > 0) {
        // this.user_apps = user.apps;
// #todo
        this.user_apps = ['booking', 'catalog', 'pos', 'config', 'documents'];
      }    
    });
  }

  public onSelect(app: any) {
    window.location.href = this.apps[app].url;
  }


}